body {
  font-family: 'Open Sans', sans-serif;
  height: 100%;
  max-width: 990px;
  margin: auto;
  padding: 0 1em;
/*  text-align: center;*/
}

/* All header CSS*/

header {
  padding: 1em 0 .5em 0;
  border-bottom: 1px solid black;
}

/* Add media query for more responsive design
   remove the floats to center the page when it shrinks */
nav {
  float: right;
}

ul {
  text-align: center;
  padding-top: .75em;
  color: blue;
}

li {
  display: inline;
  padding: .5em;
}

.name {
  font-size: 2em;
  font-weight: 700;
  padding-left: 5px;
  float: left;
  text-align: center;
}

.clearfix:after, .clearfix:before {
    content: " ";
    display: table;
}

.clearfix:before{
    content: " ";
    display: table;
}

.clearfix:after {
    clear: both;
}

/* Main CSS*/

.index {
  max-width: 500px;
}

main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100%;
  max-width: 700px;
  margin: auto;
  margin-top: 20px;
  margin-bottom: 20px;
}

.head {
  text-align: center;
  font-size: 32px;
  margin-bottom: 1.5em;
}

section {
  margin-bottom: 2em;
}

.icon {
  vertical-align: middle;
  color: red;
  padding-right: .25em;
}

h2 {
  line-height: 1;
  font-size: 1.5em;
  font-weight: 400;
  margin-left: 1.4em;
  text-indent: -1.2em;
}

p {
  line-height: 1.5;
  margin-left: 2.25em;
  font-weight: 300;
}

a {
  text-decoration: none;
  color: inherit;
}

/* Resume CSS*/

.resume {
  max-width: 500px;
}

.index h1, .resume h1, .portfolio h1, .technology h1, .contact h1{
  font-size: 2em;
  text-align: center;
  margin-top: .5em;
}

.resume h2 {
  text-decoration: underline;
  font-size: 17px;
  margin-top: 1em;
}

.resume .res ul {
  text-align: left;
  text-indent: .75em;
  color: black;
}

.resume .res ul li {
  margin-top: 3px;
  font-size: 14px;
  padding: .1em;
  display: block;
}

/* Portfolio CSS */

.portfolio {
  width: 600px;
  z-index: 2;
}

.port {
  margin-top: 1em;
  padding-bottom: 20px;
  border-bottom: 1px solid black;
}

.portfolio .port h2 {
  font-size: 1.5em;
}

.portfolio .port p {
  margin-top: 10px;
}

.portfolio .port p a{
  color: blue;
}

.portfolio .port p a:clicked{
  color: blue;
}

/* Technology CSS */

.tech {
  margin-top: 1em;
}

.tech p {
  margin-top: 10px;
}

.contact {
  max-width: 700px;
}

.info {
  margin-top: 20px;
  /*text-align: center;*/
}

.contact .info p {
  color: blue;
  margin-top: 20px;
  font-size: 2.2em;
}

 /* Footer CSS */

footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  bottom: 0;
  padding-bottom: 10px;
  max-width: 990px;
  text-align: center;
  font-size: 12px;
}

.foot {
  margin: 0;
}

.skill {
  text-decoration: underline;
}



